import { Box as MantineBox, BoxProps as MantineBoxProps, DefaultProps } from '@mantine/core';
import { FC, PropsWithChildren, forwardRef } from 'react';
import { TwBaseProps, TwStyleSystemProps } from '../../types';
import { styled } from '../../utils';

// TODO: Need to see if to limit here
export interface BoxProps
  extends TwBaseProps,
    PropsWithChildren,
    Omit<MantineBoxProps, keyof DefaultProps>,
    TwStyleSystemProps {}

const Root = styled(MantineBox)<MantineBoxProps>(() => ({}));

export const Box: FC<BoxProps> = forwardRef((props, ref) => {
  return <Root ref={ref} {...props} />;
});
